import { Component, OnInit } from '@angular/core';
import { TahapService } from '../tahap.service';
import { Router } from '@angular/router';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPegawaiThrService } from 'src/app/sdk/payroll/services';

@Component({
    selector: 'app-tahap',
    templateUrl: './tahap.component.html',
    styleUrls: ['./tahap.component.scss']
})
export class TahapComponent implements OnInit {

    constructor(
        private tahapService: TahapService,
        private payrollPegawaiThrService: PayrollPegawaiThrService,
        private router: Router,
        private pesan: PesanService,
        private spinner: NgxSpinnerService,
    ) { }

    periode: Date = new Date();
    ngOnInit() {
        this.tahapService.periode.next(this.periode);

        this.tahapService.getNext().subscribe(
            data => {
                this.if_next = data;
            }
        )
    }

    cekApakahPeriodeAvaible() {
        this.spinner.show()
        let param = {
            periode: this.periode,
            cek_data: this.periode
        }
        this.payrollPegawaiThrService.getPayrollPegawaiThrs(JSON.stringify(param)).subscribe(
            data => {
                console.log(data.data)
                this.spinner.hide()
                if (data.message == 'stop') {
                    console.log(data.message)
                    this.pesan.pesanWarningForm('Payroll periode ini belum ada, minimal buat draft');
                    this.if_next = false;
                    return false;
                }

                if (data.data['total'] > 0) {
                    this.pesan.pesanWarningForm('Periode ini sudah digunakan, gunakan tanggal Lain');
                    this.if_next = false;
                }
                else {
                    this.if_next = true;

                }
                return false
            },
            err => {
                this.spinner.hide()
                return false
            }
        )
    }


    ngModelChange() {

        this.tahapService.periode.next(this.periode);
        this.cekApakahPeriodeAvaible()
    }


    index = 'First-content';
    current = 0;

    pre(): void {
        this.if_next = true
        this.current -= 1;
        this.changeContent();
    }

    if_next: boolean = true;
    next(): void {
        console.log(this.if_next)
        if (this.current == 0) {
            this.spinner.show()
            let param = {
                periode: this.periode,
                cek_data: this.periode
            }
            this.payrollPegawaiThrService.getPayrollPegawaiThrs(JSON.stringify(param)).subscribe(
                data => {
                    console.log(data.data)
                    this.spinner.hide()
                    if (data.data['total'] > 0) {
                        this.pesan.pesanWarningForm('Periode ini sudah digunakan, gunakan tanggal Lain');
                        this.if_next = false;
                        return false;
                    }
                    else {
                        this.if_next = true;
                        this.current += 1;
                        this.changeContent();
                    }

                },
                err => {
                    this.spinner.hide()
                }
            )
        }
        else {
            if (this.if_next) {
                this.current += 1;
                this.changeContent();
            }
            else {
                this.pesan.pesanWarningForm('mohon selesaikan tahap ini!')
            }

        }


    }

    done(): void {
        if (this.if_next) {
            this.current += 1;
            this.router.navigate(['/hr/payroll/module/laporan/bonus/list']);
        }
        else {
            this.pesan.pesanWarningForm('mohon selesaikan tahap ini!')
        }

    }

    changeContent(): void {
        switch (this.current) {
            case 0: {
                this.index = 'First-content';
                break;
            }
            case 1: {
                this.index = 'Second-content';
                break;
            }
            case 2: {
                this.index = 'third-content';
                break;
            }
            default: {
                this.index = 'error';
            }
        }
    }


}
