import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { ThrRoutingModule } from './thr-routing.module';
import { NgZorroAntdModule, NzSelectModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { ListComponent } from './list/list.component';
import { EditComponent } from './edit/edit.component';


@NgModule({
    declarations: [ListComponent, EditComponent],
    imports: [
        CommonModule,
        ThrRoutingModule,
        NgZorroAntdModule,
        NzSelectModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]
})
export class ThrModule { }
