import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class TahapService {

    constructor() { }

    periode = new BehaviorSubject<Date>(null);
    getPeriode() {
        return this.periode.asObservable();
    }

    next = new BehaviorSubject<boolean>(true);
    getNext() {
        return this.next.asObservable()
    }

}
