import { Component, OnInit } from '@angular/core';
import { TahapService } from '../tahap.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollPegawaiBonusService, PayrollPegawaiThrService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NzNotificationService } from 'ng-zorro-antd';

@Component({
    selector: 'app-tahap-confirm',
    templateUrl: './tahap-confirm.component.html',
    styleUrls: ['./tahap-confirm.component.scss']
})
export class TahapConfirmComponent implements OnInit {

    constructor(
        private tahapService: TahapService,
        private spinner: NgxSpinnerService,
        private payrollPegawaiThrService: PayrollPegawaiThrService,
        private pesan: PesanService,
        private notification: NzNotificationService
    ) { }

    periode: Date = new Date();
    is_confirm: boolean = false;
    ngOnInit() {
        this.tahapService.getPeriode().subscribe(
            data => {
                if (data) {
                    this.periode = data;
                }
            }
        )
        this.tahapService.next.next(false)
    }

    confirm() {
        let param: any = {
            periode: this.periode,
            confirm: true
        }
        this.payrollPegawaiThrService.putPayrollPegawaiThrsId({ id: '000', body: param }).subscribe(
            data => {
                this.spinner.hide();
                this.is_confirm = true;
                this.tahapService.next.next(true)
            }
        )
    }

}
