import { ActivatedRoute } from '@angular/router';
import { Component, OnInit, ViewChild } from '@angular/core';
import { NzTableComponent, NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { Subject } from 'rxjs';
import { VirtualDataInterface } from '../../../proses/payroll/tahap-review/tahap-review.component';
import { takeUntil } from 'rxjs/operators';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollBankOutService, PayrollPegawaiThrService } from 'src/app/sdk/payroll/services';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { DetailPayrollComponent } from '../../../shared/detail-payroll/detail-payroll.component';
import * as XLSX from 'xlsx';
import * as FileSaver from 'file-saver';


const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {




    constructor(
        private spinner: NgxSpinnerService,
        private activatedRoute: ActivatedRoute,
        private payrollPegawaiThrService: PayrollPegawaiThrService,
        private payrollBankOutService: PayrollBankOutService,
        private pesan: PesanService,
        private notification: NzNotificationService,
        private drawerService: NzDrawerService
    ) { }




    periode: Date;
    ngOnInit(): void {
        this.activatedRoute.paramMap.subscribe(
            data => {
                if (data.get('periode')) {
                    this.periode = new Date(data.get('periode'));
                    this.getRekap()
                }
            }
        )

    }


    listData: any[] = []
    rekap: any[] = []
    payment: any = {};
    getRekap() {
        this.spinner.show()
        let param: any = {
            periode: this.periode,
        }
        this.payrollPegawaiThrService.getPayrollPegawaiThrs(JSON.stringify(param)).subscribe(
            data => {
                let datas: any = data.data;
                this.listData = datas.list;
                this.rekap = datas.rekap;
                this.payment = datas.payment[0];
                this.spinner.hide()
            }
        )
    }

    generatePayemnt() {
        this.spinner.show()
        let param: any = {
            periode: this.periode,
            perintah: 'generate',
            module: 'payroll'
        }
        this.payrollBankOutService.postPayrollBankOuts(param).subscribe(
            data => {
                this.getRekap()
                this.spinner.hide()
                this.pesan.pesanBerhasilForm('payment berhasil di generate');
            }
        )
    }
    downloadReport() {
        //download / export ke excl
        let worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.listData);

        const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };

        const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
        this.saveAsExcelFile(excelBuffer, 'Report ');
    }

    listDwnloadPayment = [];
    downloadPayement() {
        this.spinner.show()
        let param: any = {
            periode: this.periode,
            module: 'payroll'
        }
        this.payrollBankOutService.getPayrollBankOuts(JSON.stringify(param)).subscribe(
            data => {
                this.listDwnloadPayment = [];
                let payroll = this.listData;
                let bank_out = data.data['out']
                let nama_bank = data.data['nama_bank']

                payroll.forEach((element, index) => {
                    this.listDwnloadPayment.push(element);
                    nama_bank.forEach((bank, index_bank) => {
                        this.listDwnloadPayment[index][bank.nama_bank] = 0;
                        this.listDwnloadPayment[index][bank.nama_bank + '_rekening'] = '';
                    })

                    let share = 0;
                    let terpakai = null;
                    let cek = bank_out.filter((kom, i) => {
                        if (kom.id_pegawai == element.id_pegawai) {

                            // bank_out[i]['share'] = bank_out[i].dari_nilai_total > share ? (bank_out[i].jumlah == null ? (bank_out[i].dari_nilai_total - share) : bank_out[i].jumlah) : 0;
                            // share = share + bank_out[i]['share'];

                            //jadiian kakiri
                            nama_bank.forEach((bank, index_bank) => {
                                if (kom.nama_bank == bank.nama_bank) {
                                    this.listDwnloadPayment[index][bank.nama_bank] = bank_out[i]['jumlah_transfer'];
                                    this.listDwnloadPayment[index][bank.nama_bank + '_rekening'] = bank_out[i].no_rekening;
                                }

                            })
                            return true;
                        }
                        else {
                            share = 0;
                        }

                    })
                    // this.listDwnloadPayment[index]['item'] = cek;
                });


                //download / export ke excl
                let worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.listDwnloadPayment);

                const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };

                const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });
                this.saveAsExcelFile(excelBuffer, 'bank');


                this.spinner.hide()
                console.log(this.listDwnloadPayment)
            }


        )
    }

    private saveAsExcelFile(buffer: any, fileName: string): void {
        const data: Blob = new Blob([buffer], { type: EXCEL_TYPE });
        FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + '.xlsx');
    }



    // PREVIEW
    id_pegawai_payroll: string = '';
    detailPerPegawai(id_pegawai_payroll) {
        this.id_pegawai_payroll = id_pegawai_payroll;
        this.openComponent()
    }

    openComponent(): void {
        const drawerRef = this.drawerService.create<DetailPayrollComponent, { value: string }, string>({
            nzTitle: 'Detail Payroll',
            nzContent: DetailPayrollComponent,
            nzWidth: '800px',
            nzContentParams: {
                value: this.id_pegawai_payroll
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {
            console.log(data);
            if (typeof data === 'string') {
                this.id_pegawai_payroll = data;
            }
        });
    }


}
